.page-footer {
  color: $white;
  background-color: $black;
}

.page-footer__container {
  padding-top: 80px;
  padding-bottom: 74px;
}

.page-footer__wrapper {
  display: grid;
  grid-template-columns: minmax(min-content, 500px) 1fr max-content;
  column-gap: 91px;
}

.page-footer__nav {
  display: flex;
  justify-content: flex-start;
  padding: 16px 0 0;

  & ul {
    @include font($poppins, normal, 500);
    font-size: 16px;
    line-height: 21px;
    margin: 0;
    padding: 0;
    list-style: none;
    color: $white;
  }

  & ul:first-of-type {
    margin-right: 19%;
  }

  & li {
    margin: 0 0 21px;
  }

  & a {
    @include font($poppins, normal, 500);
    font-size: 16px;
    line-height: 21px;
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: $white;
  }

  & a:hover,
  & a:focus {
    opacity: 0.7;
  }

  & a:active {
    opacity: 0.5;
  }
}
